.cartmodal {
        position: fixed;
        top: 0;
        right: 0;
        width: 300px;
        height: 90%;
        background-color: #f8f9fa;
        padding: 20px;
        overflow-y: auto;
        box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
        background-color: white;
            padding: 20px;
            border-radius: 10px;
            width: 400px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}


.carttitle {
    margin: 0;
    margin-bottom: 20px;
    color: #343a40;
}

.cartclosebtn {
    position: absolute;
    top: 20px;
    right: 20px;
    background: none;
    border: none;
    font-size: 20px;
    color: black;
}

.cartlist {
    list-style-type: none;
    padding: 0;
}

.cartitem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.cartimg {
    width: 50px;
    height: 50px;
    object-fit: cover;
    margin-right: 10px;
}
.cartitemtitle{
    display: flex;
}


.buttonoff{
    margin-top: 340px;
    justify-content: center;
    background-color: crimson;
        color: white;
        padding: 10px 20px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
}
.countmin{
    background-color: crimson;
        color: white;
        padding: 5px 10px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
}
.countplus{
    background-color: crimson;
